    monospace;
}

.footer {
  position: fixed;
  left: 0;
  bottom: 0;
  width: 100%;
  background-color: black;
  color: white;
  text-align: center;
  padding: 10px;

}

#mainStyle {
  margin-top: 46px;
  height: 600px;
  background-color: rgb(255, 255, 255);
