/* FOOTER */

.footer-distributed {
  background: #6C757D;
  box-shadow: 0 1px 1px 0 rgba(0, 0, 0, 0.12);
  box-sizing: border-box;
  width: 100%;
  text-align: left;
  font: bold 16px sans-serif;
  padding: 55px 50px;
}

.footer-distributed .footer-left,
.footer-distributed .footer-center,
.footer-distributed .footer-right {
  display: inline-block;
  vertical-align: top;
}

.footer-distributed .footer-left {
  width: 40%;
}

.footer-distributed h3 {
  color: #ffffff;
  font: normal 36px 'Open Sans', cursive;
  margin: 0;
}

.footer-distributed h3 span {
  color: lightseagreen;
}

.footer-distributed .footer-links {
  color: #ffffff;
  margin: 20px 0 12px;
  padding: 0;
}

.footer-distributed .footer-links a {
  display: inline-block;
  line-height: 1.8;
  font-weight: 400;
  text-decoration: none;
  color: inherit;
}

.footer-distributed .footer-company-name {
  color: #222;
  font-size: 14px;
  font-weight: normal;
  margin: 0;
}

.footer-distributed .footer-center {
  width: 35%;
}

.footer-distributed .footer-center i {
  background-color: #33383b;
  color: #ffffff;
  font-size: 25px;
  width: 38px;
  height: 38px;
  border-radius: 50%;
  text-align: center;
  line-height: 42px;
  margin: 10px 15px;
  vertical-align: middle;
}

.footer-distributed .footer-center i.fa-envelope {
  font-size: 17px;
  line-height: 38px;
}

.footer-distributed .footer-center p {
  display: inline-block;
  color: #ffffff;
  font-weight: 400;
  vertical-align: middle;
  margin: 0;
}

.footer-distributed .footer-center p span {
  display: block;
  font-weight: normal;
  font-size: 14px;
  line-height: 2;
}

.footer-distributed .footer-center p a {
  color: lightseagreen;
  text-decoration: none;
  ;
}

.footer-distributed .footer-links a:before {
  content: "|";
  font-weight: 300;
  font-size: 20px;
  left: 0;
  color: #fff;
  display: inline-block;
  padding-right: 5px;
}

.footer-distributed .footer-links .link-1:before {
  content: none;
}

.footer-distributed .footer-right {
  width: 20%;
}

.footer-distributed .footer-company-about {
  line-height: 20px;
  color: #92999f;
  font-size: 13px;
  font-weight: normal;
  margin: 0;
}

.footer-distributed .footer-company-about span {
  display: block;
  color: #ffffff;
  font-size: 14px;
  font-weight: bold;
  margin-bottom: 20px;
}

.footer-distributed .footer-icons {
  margin-top: 25px;
}

.footer-distributed .footer-icons a {
  display: inline-block;
  width: 35px;
  height: 35px;
  cursor: pointer;
  background-color: #33383b;
  border-radius: 2px;

  font-size: 20px;
  color: #ffffff;
  text-align: center;
  line-height: 35px;

  margin-right: 3px;
  margin-bottom: 5px;
}

@media (max-width: 880px) {

  .footer-distributed {
    font: bold 14px sans-serif;
  }

  .footer-distributed .footer-left,
  .footer-distributed .footer-center,
  .footer-distributed .footer-right {
    display: block;
    width: 100%;
    margin-bottom: 40px;
    text-align: center;
  }

  .footer-distributed .footer-center i {
    margin-left: 0;
  }

}

/* Reservation */

.calendar-container {
  margin-top: 20px;
  margin-left: 60px;
  width: 500px;
  overflow: hidden;
  background-color: #fff;
}

.month-header {
  background-color: #ecf0f1;
  border: 1px solid #ccc;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  border-radius: 8px 8px 0px 0px;
  padding: 10px;
  display: flex;
  font-weight: bold;
  justify-content: space-between;
  align-items: center;
}

.month-header button {
  background-color: #ecf0f1;
  border: none;
  font-weight: bold;
  padding: 5px 10px;
  cursor: pointer;
  border-radius: 4px;
}

.calendar-days {
  display: grid;
  grid-template-columns: repeat(7, 1fr);
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  border: 1px solid #eae9e9;
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.1);
  border-radius: 0px 0px 8px 8px;
  gap: 5px;
  padding-bottom: 20px;
}

.calendar-day {
  padding: 10px;
  text-align: center;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.calendar-day:hover {
  background-color: #ecf0f1;
}

.calendar-day.selected {
  background-color: #DC3545;
  color: #fff;
}

.calendar-day.disabled {
  color: #ccc;
  cursor: not-allowed;
}

.time-section {
  margin-top: 20px;
  margin-left: 100px;
}

.time-buttons {
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
  margin: 15px 70px 5px 5px;
}

.time-button {
  background-color: #ffffff;
  color: #DC3545;
  border: 1px solid #DC3545;
  width: 150px;
  padding: 5px;
  margin: 5px 0px 5px 0px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.time-button:hover {
  background-color: #f86959;
}

.time-button.selected {
  background-color: #DC3545;
  color: #fff;
}

.reserve-button {
  background-color: #DC3545;
  color: #fff;
  border: none;
  border-radius: 5px;
  padding: 10px;
  width: 1250px;
  cursor: pointer;
  margin: 10px 20px 10px 20px;
  transition: background-color 0.3s ease;
}

.reserve-button:disabled {
  background-color: #ccc;
  cursor: not-allowed;
}

.reserve-button:hover:enabled {
  background-color: #DC3545;
}

.selected-date {
  margin-top: 20px;
  font-weight: bold;
}


.texxt {
  text-align: center;
}

.texxt h1 {
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}

.select p {
  font-size: medium;
}

.detail {
  font-size: medium;
  margin-left: 10px;
}

